.app {
  display: flex;
  align-items: center;
  justify-content: left;
  height: 100vh;
  background: linear-gradient(
      rgba(255, 255, 255, 0.7),
      rgba(255, 255, 255, 0.3)
    ),
    url("imagenes/FONDO.png");
  background-size: cover;
  background-position: center;
}
body {
  margin: 0
}

form{
    background-color: white;
    margin-left: 20vh;
    padding: 0px 60px;
    border-radius: 10px;
}



h1{
    color: rgb(77, 1, 77);
    text-align: center;
}

button{
    width: 100%;
    height: 50px;
    padding: 10px;
    border: none;
    background-color: rebeccapurple;
    color: white;
    border-radius: 5px;
    font-weight: bold;
    font-size: 18px;
    cursor: pointer;
    margin-top: 15px;
    margin-bottom: 30px;
}